section.motherday-loading{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100vh;
	background-color: white;
	z-index: 5;
	&.active{
		animation: opacity .8s 2s forwards;
	}
	.loading1,.loading2,.loading3{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vh;
	}
	.loading1{
		z-index: 2;
		.loading1Img{
			height: 100%;
			width: auto;
			position: absolute;
			left: 50%;
			bottom: 0;
			transform: translateX(-50%);
			&.active{
				animation: loading1 1s 1s forwards;
			}
		}
	}
	.loading2{
		.loading2Img{
			height: 100%;
			width: auto;
			position: absolute;
			left: 50%;
			top: 0;
			transform: translateX(-50%);
			z-index: 1;
			&.active{
				animation: loading2 1s 1s forwards;
			}	
		}
	}
	.loading3{
		.loading3Img{
			height: 100%;
			width: auto;
			position: absolute;
			left: 50%;
			bottom: 0;
			transform: translateX(-50%);
			&.active{
				animation: loading3 1s 1s forwards;
			}
		}
	}
	@keyframes opacity{
		0%{opacity: 1;}
		100%{opacity: 0; pointer-events: none}
	}

	@keyframes loading1{
		0%{transform: translateX(-50%)}
		100%{transform: translateX(-60%) translateY(20%) rotate(20deg); opacity: 0;}
	}

	@keyframes loading2{
		0%{transform: translateX(-50%)}
		100%{transform: translateX(-45%) translateY(-50%) rotate(30deg); opacity: 0;}
	}

	@keyframes loading3{
		0%{transform: translateX(-50%)}
		100%{transform: translateX(-45%) translateY(25%) rotate(-20deg); opacity: 0;}
	}
}


section.event-2018-may{
	height: 100vh;
	width: 100%;
	position: relative;
	overflow: hidden;
	&.active{
		&:before{
			@media (min-width:$sm){
				animation: borderAnimation 1s 2s forwards;
			}
		}
	}
	&.static{
		&:before{
			border: solid 60px white;
			@media (max-width:$sm){
				border: solid 0px white;
			}
		}
	}
	&:before{
		content: '';
		display: block;
		border: solid 0px white;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
		@keyframes borderAnimation {
			0%{ border: solid 0px white;}
			100%{ border: solid 60px white;}
		}
	}
	@media (max-width:1366px){
		height: 70vh;
		padding: 30px 50px;
		min-height: 790px;
	}
	@media (max-width:$sm){
		height: auto;
		min-height: auto;
		background-image: url('../img/main_ipad.jpg');
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		padding: 35%;
	}
	@media (max-width:$xxs){
		margin-top: -1px;
		background-image: url('../img/main_mobile.jpg');
		padding-bottom: 143%;
	}
	.banner-left{
		position: absolute;
		top: -10px;
		left: -90px;
		z-index: 3;
		max-width: 85%;
		transform-origin: left top;
		opacity: 0;
		&.active{
			animation: leftBottomFlower 1s forwards ease-out;
		}
		&.static{
			transform: scale(1) rotate(0deg); opacity: 1
		}
		img{
			max-height:80vh;
		}
		@media (max-width:$sm){
			display: none;
		}

		@keyframes leftBottomFlower {
			0%{ transform: scale(.9) rotate(-3deg); opacity: 0;}
			100%{ transform: scale(1) rotate(0deg); opacity: 1}
		}
	}
	.banner{
		width: 100%;
		height: 100%;
		background-image: url('../img/main_bg.jpg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: relative;
		overflow: hidden;
		@media (max-width:$sm){
			display: none;
		}

		.banner-right{
			position: absolute;
			bottom: 0px;
			right: -40px;
			background-image: url('../img/banner_right.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			padding: 15% 22%;
			transform-origin: right bottom;
			transform: scale(.7);
			opacity: 0;
			&.active{
				animation: rightBottomFlower 1s forwards ease-out;
			}
			&.static{
				opacity: 1;
				transform: scale(1);
			}
			
			@keyframes rightBottomFlower {
				0%{ transform: scale(.9) rotate(-3deg); opacity: 0;}
				100%{ transform: scale(1) rotate(0deg); opacity: 1}
			}
		}
	}
	.banner-title{
		position: absolute;
		top: 30%;
		left: 20%;
		&.active{
			h3{
				animation: titleSlideUp .8s 2s forwards;
			}
			p{
				animation: textSlide 1s 2.5s forwards;
			}
			ul{
				li{
					&.s1{
						animation: textFadeIn 2s 1.5s forwards ease-in-out;
					}
					&.s2{
						animation: textFadeIn2 1.7s 1.7s forwards ease-in-out;
					}
					&.s3{
						animation: textFadeIn3 1.8s 1.8s forwards ease-in-out;
					}
				}
			}
		}
		&.static{
			h3{
				background-position: center 0;
			}
			p{
				opacity: 1;
			}
			ul{
				li{
					&.s1,&.s2,&.s3{
						opacity: 1;
					}
				}
			}
		}
		@media (max-width:$md){
			left: 13%;
		}
		@media (max-width:$sm){
			opacity: 0;
		}
		h3,p{
			font-size: 0%;
			text-indent: -99999px;
			background: {
				position: center;
				size: contain;
				repeat: no-repeat;
			}
		}

		ul{
			text-align: center;
			margin-bottom: 30px;
			&:first-child() {
				margin-bottom: 50px;
			}
			@media (max-width:$break){
				margin-bottom: 20px;
				&:first-child() {
					margin-bottom: 40px;
				}
			}
			li{
				display: inline-block;
				margin-right: 18px;
				opacity: 0;
				&:last-child {
					margin-right: 0;
				}
				img{
					@media (max-width: 2100px){
						max-height: 90px;
					}
					@media (max-width:$break){
						max-height: 75px;
					}
				}
			}
			@keyframes textFadeIn{
				0%{ opacity: 0;}
				30%{ opacity: 1;}
				60% {opacity: .4;}
				100%{ opacity: 1;}
			}
			@keyframes textFadeIn2{
				0%{ opacity: 0;}
				20%{ opacity: .7;}
				40% {opacity: .3;}
				80% {opacity: .75;}
				100%{ opacity: 1;}
			}
			@keyframes textFadeIn3{
				0%{ opacity: 0;}
				20%{ opacity: .6;}
				50%{ opacity: .8;}
				100%{ opacity: 1;}
			}
		}
		p{
			width: 430px;
			height: 40px;
			background-image: url('../img/text4.png');
			margin: auto;
			position: relative;
			opacity: 0;
			@media (max-width: 2100px){
				width: 390px;
			}
			@media (max-width:$break){
				width: 290px;
			}
			// &:before{
			// 	position: absolute;
			// 	content: '';
			// 	right: 0;
			// 	top: 0;
			// 	width: 100%;
			// 	height: 100%;
			// 	display: block;
			// 	background-size: cover;
			// 	background-image: url('../img/main_bg.jpg');
			// }
			@keyframes textSlide{
				from{
					opacity: 0;
				}
				to{
					opacity: 1;
				}
			}
		}
	}
	.ppl {
		@media (max-width:$sm){
			display: none;
		}
		img{
			position: absolute;
			bottom: -20px;
			right: 0;
			width: auto;
			max-height:calc(100vh - 104px);
			z-index: 3;
			opacity: 0;
			&.active{
				animation: fadeUpPeopel 1.3s 1s forwards;
			}
			&.static{
				opacity: 1;
			}
			@keyframes fadeUpPeopel {
				0%{opacity: 0;}
				100%{opacity: 1;}
			}
			
			@media (min-width: 2100px){
				right: 10%;
			}
			@media (max-width: 1366px){
				max-height: 80vh;
				min-height: 560px;
			}
			@media (max-width: $md){
				min-height: 460px;
			}
		}
	}
}